import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserauthenticatorService } from '../userauthenticator.service';
import { ListingsserviceService } from '../listingsservice.service';

@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css']
})
export class WishlistComponent implements OnInit {

  user = '' ;
  myWishlist ;
  load_completed: boolean ;

  constructor(  private route: Router , private userAuth: UserauthenticatorService, private listingService: ListingsserviceService ) { }

  removefromWishlist(event): void {
    const id = +(event.target.id.split('-')[1]) ;
    this.listingService.removefromWishlist( this.user , id ) ;
    const tempListings = this.myWishlist.filter( (listing) => {
      console.log( listing.listing_id !== id )
      return ( listing.listing_id !== id ) ;
    } ) ;
    this.myWishlist = JSON.parse( JSON.stringify( tempListings ) ) ;
    alert("Item Removed Successfully...") ;
  }

  getWishlist(): void {
    this.listingService.fetchWishlist( this.user ).then( (result) => {
      this.myWishlist = result ;
      this.load_completed = true ;

    }) ;
  }

  ngOnInit() {
    this.load_completed = false ;
    this.userAuth.isLoggedIn().then( (result) => {
      this.user = this.userAuth.user_id ;
      if (!result) {
        this.route.navigate(['home/login']) ;
      } else {
        this.getWishlist() ;
      }
    } ) ;
  }

  logout(): void {
    this.userAuth.logOut() ;
    this.route.navigate( ['home/login'] ) ;
  }

}
