import { Component, OnInit } from '@angular/core';
import { UserauthenticatorService } from '../userauthenticator.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ListingsserviceService } from '../listingsservice.service';


@Component({
  selector: 'app-listings',
  templateUrl: './listings.component.html',
  styleUrls: ['./listings.component.css'],
})
export class ListingsComponent implements OnInit {

   user = '' ;
   listings;
   filteredListings ;
   card_class = 'card mb-4 my-card' ;
   search_query = "" ;

   // Always use filter by price first
   // then filter by condition 
   // then sort
   // search is at real-time

   selected_price_filter = 1 ;
   price_filters = [ {l: 0 , r: 0} , { l : 0 , r : 100000000 } , { l : 0 , r : 500 } , { l : 500 , r : 1000 } , { l : 1000 , r : 2000 } ,
                   { l: 2000, r: 100000000 }];

   selected_sort = 1 ;
   sort_button_classes = [ 'btn btn-light my-active' , 'btn btn-light' , 'btn btn-light' ] ;

   conditon_checkboxes = [ true , true , true , true ] ;

  constructor(private route: Router , private router: ActivatedRoute, private userAuth: UserauthenticatorService,
               private listingService: ListingsserviceService) { }

  getListings() {
    this.listingService.fetchListings().then( (result) => {
      if (result === true) {
        this.listings = this.listingService.listings ;
        this.filteredListings = JSON.parse( JSON.stringify( this.listings ) ) ;
        this.sortListings(1) ;
      }
    } ) ;
  }

  ngOnInit() {
    this.userAuth.isLoggedIn().then( (result) => {
      this.user = this.userAuth.user_id ;
      if (!result) {
        this.route.navigate(['home/login']) ;
      } else {
        this.getListings() ;
      }
    } ) ;
    this.search_query = '' ;
   }

  addToWishlist(event): void{
    const id = +(event.target.id.split('-')[1]) ;
    this.listingService.addListingToWishlist( this.userAuth.user_id , id ) ;
    alert("Item Added Successfully...") ;
  }

  sortListings( sort_id: number ): void {
    const tempListings = JSON.parse( JSON.stringify( this.filteredListings ) ) ;

    if (sort_id === 1) {

      this.sort_button_classes = [ 'btn btn-light my-active' , 'btn btn-light' , 'btn btn-light' ] ;
      tempListings.sort( function (a , b) { return ( a['listing_id'] < b['listing_id'] ) ; } ) ;

    } else if ( sort_id === 2) {

      this.sort_button_classes = [ 'btn btn-light' , 'btn btn-light my-active' , 'btn btn-light' ] ;
      tempListings.sort( function (a , b) { return ( a.price < b.price ) ; } ) ;

    } else {

      this.sort_button_classes = [ 'btn btn-light' , 'btn btn-light' , 'btn btn-light my-active' ] ;
      tempListings.sort( function (a, b) { return ( a.price > b.price ) ;} ) ;

    }

    this.filteredListings = JSON.parse( JSON.stringify( tempListings ) ) ;
  }


  sortClicked( event ): void {
    let sort_id = +(event.target.id.split("-")[1]) ;
    this.sortListings(sort_id) ;
    this.selected_sort = sort_id ;

  }

  // filter by price
  onSelectionChange(event): void {
    this.filterbyPrice( +event.target.value ) ;
    this.selected_price_filter = +(event.target.value) ;
    this.conditionFilter() ;

  }

  // filter by condition
  conditionFilterClick(): void {
    this.filterbyPrice( this.selected_price_filter ) ;
    this.conditionFilter() ;
  }

  filterbyPrice(value: number) {
    const tempListings = this.listings.filter( (listing) => {
      return ( listing['price'] >= this.price_filters[value].l && listing['price'] <= this.price_filters[value].r ) ;
    } ) ;
    this.filteredListings = JSON.parse( JSON.stringify( tempListings ) ) ;
  }

  conditionFilter(): void {

    const tempListings = this.filteredListings.filter( (listing) => {

      if (this.conditon_checkboxes[0] === true && listing['book_condition'] === 'new') {
        return true ;
      }
      if (this.conditon_checkboxes[1] === true && listing['book_condition'] === 'almost new') {
        return true ;
      }
      if (this.conditon_checkboxes[2] === true && listing['book_condition'] === 'slight damage') {
        return true ;
      }
      if (this.conditon_checkboxes[3] === true && listing['book_condition'] === 'worn') {
        return true ;
      }
      return false ;
    } ) ;
   // console.log( tempListings ) ;
    this.filteredListings = JSON.parse( JSON.stringify( tempListings ) ) ;

    this.sortListings( this.selected_sort ) ;

  }

  mouseEnter(): void {
    this.card_class = 'card mb-4 my-card shadow p-3 mb-5 bg-white rounded' ;
  }

  mouseLeave(): void {
    this.card_class = 'card mb-4 my-card' ;
  }

  logout(): void {
    this.userAuth.logOut() ;
    this.route.navigate( ['home/login'] ) ;
  }

  messages(): void {
    this.route.navigate(['messages']) ;
  }
}

