import { Component, OnInit } from '@angular/core';
import { Users } from '../users' ;
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { UserauthenticatorService } from '../userauthenticator.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  validForm: boolean ;
  newUser: Users ;


  constructor(private http: HttpClient, private userAuth: UserauthenticatorService, private route: Router) {
    this.validForm = false ;
  }

  ngOnInit() {
    this.resetForm() ;
  }

  resetForm() {
    this.newUser = { firstName : '' , lastName : '' , username : '' , email : '' , password : '', repassword : '' } ;
  }

  validateFirstname() {
    console.log("I'm called") ;
    console.log(this.newUser.firstName) ;
    this.newUser.firstName = this.newUser.firstName.replace(/[^a-zA-Z0-9@]+/, '') ;
    console.log(this.newUser.firstName) ;
  }

  validateLastname() {
    this.newUser.lastName = this.newUser.lastName.replace(/[^a-zA-Z0-9@]+/, '') ;
  }

  validateForm( check: boolean ): void {
   
    if (!check) {
      this.newUser.password = null ;
      this.newUser.repassword = null ;
    } else {
      alert('Form Submitted') ;
      this.userAuth.registerUser( this.newUser ) ;
      this.resetForm() ;
      this.route.navigate(['home/login']) ;
    }
  }

  validateEmail() {
    let reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/ ;
    return new Promise( (resolve, reject) => {
      resolve( reg.test( this.newUser.email ) === false ) ;
    } ) ;
  }


  checkform(): boolean {

    // first name should not be empty
    this.newUser.firstName = this.newUser.firstName.trim() ;
    if ( this.newUser.firstName.length === 0 ) {
      alert( 'First Name cannot be empty' ) ;
      this.validateForm(false) ;
      return ;
    }
    // trim the last name
    this.newUser.lastName = this.newUser.lastName.trim() ;

    // trim the username
    this.newUser.username = this.newUser.username.trim() ;
    if ( this.newUser.username.length === 0 ) {
      alert( 'Username cannot be empty' ) ;
      this.validateForm(false) ;
      return ;
    }

    // check whether a username exists
    this.userAuth.checkUsername(this.newUser.username).then( ( result ) => {
      //console.log("Result of checkUsername = " + result) ;
      if (!result) {
        this.newUser.username = '' ;
        alert("A user already exists with the same username") ;
        this.validateForm(false) ;
      }else {

        // trim the email
        //console.log( this.newUser ) ;
        this.newUser.email = this.newUser.email.trim() ;
        if ( this.newUser.email.length === 0 ) {
          alert( 'Email cannot be empty' ) ;
          this.validateForm(false) ;
          return ;
        }

        this.validateEmail().then( (result) => {
          //console.log(result) ;
          if (result) {
            alert("Please enter a valid email address") ;
            this.validateForm(false) ;
          } else {
            // check whether a email exists
            this.userAuth.checkEmail( this.newUser.email ).then( (result) => {

              if (!result) {
                this.newUser.email = '' ;
                alert("A user already exists with the same email") ;
                this.validateForm(false) ;
              } else {

                
                if ( this.newUser.password !== this.newUser.repassword ) {
                  alert("Passwords do not match") ;
                  this.validateForm(false) ;
                } else {
                  if ( this.newUser.password.trim() === '' ) {
                    alert("Password can't be empty") ;
                    this.validateForm(false) ;
                  }else {
                    this.validateForm(true) ;
                  }
                }

              }


            }) ;
          }

        } ) ;
      }


    } ) ;


  }

  submitForm(): void {
    this.checkform() ;
  }


}
