import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserauthenticatorService } from '../userauthenticator.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {


  constructor( private route: Router , private router: ActivatedRoute, private userAuth : UserauthenticatorService ) { }

  loginClassName = 'nav-link active' ;
  signupClassName = 'nav-link' ;

  ngOnInit() {

    this.userAuth.isLoggedIn().then( (result) => {
      if (!result) {
        this.route.navigate(['home/login']) ;
      } else {
        this.route.navigate(['listings']) ;
      }
    } ) ;
  }

}
