import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { resolve } from 'url';
import { reject } from 'q';
import { Users } from './users';

@Injectable({
  providedIn: 'root'
})
export class UserauthenticatorService {


  user_id = null ;

  constructor(private http: HttpClient) { }

  headers = new HttpHeaders()
  .set('Authorization', 'my-auth-token')
  .set('Content-Type', 'application/json');

  registerUser(newUser: Users): void {
    this.http.post('/api/register', JSON.stringify(newUser), {
      headers: this.headers
      })
      .subscribe(data => { } ) ;

  }

  loginUser( username , password ) {
    return new Promise( ( resolve , reject ) => {

      this.http.post('/api/login', JSON.stringify( { username : username , password : password } ),
      {headers: this.headers})
      .subscribe(data => {
        resolve(data) ;
      });

      } ) ;
  }

  isLoggedIn() {
    return new Promise( ( resolve , reject ) => {

    if ( localStorage.getItem( 'authenticator' ) === null ) {
      resolve(false) ;
    } else {
      const token = localStorage.getItem( 'authenticator' ) ;
      this.http.post('/api/verifyToken', { token : token } , {headers: this.headers} )
      .subscribe(data => {
        if ( data['statusCode'] === 200 ) {
          this.user_id = data['authData']['user_id'] ;
          resolve(true) ;
        } else {
          this.user_id = null ;
          resolve(false) ;
        }
      });
    }

    } ) ;
  }

  checkUsername( username: string ) {
    return new Promise( ( resolve , reject ) => {

      this.http.post('/api/verifyUser', JSON.stringify( { username : username } ),
      {headers: this.headers})
      .subscribe(data => {
        resolve(data['val']) ;
      });

      } ) ;
  }

  checkEmail( email: string ) {
    return new Promise( ( resolve , reject ) => {

      this.http.post('/api/verifyEmail', JSON.stringify( { email : email } ),
      {headers: this.headers})
      .subscribe(data => {
        resolve(data['val']) ;
      });

      } ) ;
  }


  logOut(): void {
    localStorage.removeItem( 'authenticator') ;
  }

}
