import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { reject } from 'q';

@Injectable({
  providedIn: 'root'
})
export class ListingsserviceService {

  listings ;

  constructor( private http: HttpClient ) { }

  headers = new HttpHeaders()
  .set('Authorization', 'my-auth-token')
  .set('Content-Type', 'application/json');

  addListingToWishlist( user_id: string , listing_id: number ): void {
    this.http.post('/api/addListingToWishlist', { user_id : user_id , listing_id : listing_id }, {
      headers: this.headers
      })
      .subscribe((data) => { } ) ;
  }

  removefromWishlist( user_id: string , listing_id: number ): void {
    this.http.post('/api/removefromWishlist', { user_id : user_id , listing_id : listing_id }, {
      headers: this.headers
      })
      .subscribe((data) => { } ) ;
  }

  fetchWishlist(user_id: string) {
    return new Promise( (resolve,reject) => {
      this.http.post('/api/fetchWishlist', { user_id : user_id}, {
      headers: this.headers
      })
      .subscribe((data) => {
        resolve(data) ;
      } ) ;
    } ) ;
  }

  fetchListings() {
    return new Promise( (resolve , reject ) => {
      this.http.get('/api/fetchListings', {
      headers: this.headers
      })
      .subscribe((data) => {
        this.listings = data ;
        resolve(true) ;
       } ) ;
    }) ;

  }

}
