import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { UserauthenticatorService } from '../userauthenticator.service';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username: string ;
  password: string ;
  constructor (private http: HttpClient, private route: Router ,
    private router: ActivatedRoute , private userAuth: UserauthenticatorService) {}

  ngOnInit() {
    this.route.navigate(['home/login']);
  }

  doSomething(authData): void {
    if (authData['statusCode'] === 200) {
      localStorage.setItem( 'authenticator' , authData['jwt'] ) ;
      this.route.navigate(['listings']) ;
    } else if ( authData['statusCode'] === 500) { 
      alert('No such user found') ;
    } else {
      alert('Incorrect Password') ;
    }
  }

  loginClicked(): void {

    this.userAuth.loginUser( this.username , this.password ).then ( (result) => {
      this.doSomething(result) ;
    }) ;
  }

}
